import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';


import { environment } from 'src/environments/environment';
import { catchError, delay } from 'rxjs/operators';
import { login } from '../interfaces/login';


@Injectable({
    providedIn: 'root'
})
export class LoginService {
    constructor(
        private http: HttpClient,
    ) { }
    
    httpOptions = {
        headers: new HttpHeaders({      
          'Content-Type': 'application/json',
          
        })
      }
    async getLoginData(dataLogin: login): Promise<any> {
        /**
         * This is what your API endpoint might look like:
         *
         * https://example.com/api/shop/categories/power-tools.json
         *
         * where:
         * - power-tools = slug
         */
        // return this.http.get<Category>(`https://example.com/api/shop/categories/${slug}.json`);

        // This is for demonstration purposes only. Remove it and use the code above.
        console.log("in service ")
   
        return await this.http.post(environment.baseUrl+"/Customer/GetLogin", 
        JSON.stringify(dataLogin),this.httpOptions) .pipe(
           
            catchError(this.errorHandler)
          ).toPromise();
    }
    
 // register customer details
 async registerCustUserData(dataLogin: login): Promise<any> {

    console.log(dataLogin);
    return this.http.post(environment.baseUrl+"/Customer", JSON.stringify(dataLogin),this.httpOptions).pipe(
        catchError(this.errorHandler)
      ).toPromise();
}
// profile update customer details
async editProfileCust(dataLogin: login): Promise<any> {

  console.log("profile update customer details");
  console.log(environment.baseUrl+"/Customer/"+dataLogin.customerId)
  return await this.http.get(environment.baseUrl+"/Customer/"+dataLogin.customerId).pipe(
   
      catchError(this.errorHandler)
    ).toPromise();
}
// profile update seller
async editProfileSeller(dataLogin: login): Promise<any> {

  console.log("profile update seller");
  return await this.http.get(environment.baseUrl+"/Seller/"+dataLogin.customerId,this.httpOptions).pipe(
      catchError(this.errorHandler)
    ).toPromise();
}
// register seller details
async registerSellerUserData(dataLogin: login): Promise<any> {

    console.log("sellers reg serv");
    return await this.http.post(environment.baseUrl+"/Seller", JSON.stringify(dataLogin),this.httpOptions) .pipe(
        catchError(this.errorHandler)
      ).toPromise();
}

errorHandler(error: { error: { message: string; }; status: any; message: any; }) {
    let errorMessage = '';
    if(error.error instanceof ErrorEvent) {
      errorMessage = error.error.message;
    } else {
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(errorMessage);
 }
}


